<template>
  <div class="love-album">
    <div class="header">
      <div class="header-left">
        <img class="avatar" :src="LoveAlbumData.avatar">
        <h1 class="album-title">{{ LoveAlbumData.albumTitle }}</h1>
      </div>

      <!-- 导航按钮 -->
      <div class="page-index-info">
        <button v-for="(item, index) in LoveAlbumData.headerNav" :key="index" type="button" class="page-index-btn"
          :class="{ 'active': index === flipToIndex }" @click="flipTo(item.flipToIndex)">
          {{ item.title }}
        </button>
      </div>

      <!-- 翻页按钮 -->
      <div class="page-nav-container">
        <div class="page-nav">
          <button type="button" class="btn-prev" @click="flipPrev">
            <span class="heart-icon">❤</span> Previous
          </button>
          <button type="button" class="btn-next" @click="flipNext">
            Next <span class="heart-icon">❤</span>
          </button>
        </div>

        <!-- 在一起的时间 -->
        <div class="love-time-info">
          <div class="time-title">琳可爱，我们已经相恋了</div>
          <div v-if="togetherTimeResult" id="elapseClock">
            <span class="heart-icon">❤</span>
            {{ togetherTimeResult }}
          </div>
        </div>
      </div>

    </div>

    <div class="container">
      <div class="flip-book html-book stf__parent" id="demoBookExample">
        <!-- 封面 -->
        <div class="page page-cover page-cover-top" data-density="hard">
          <div class="page-content">
            <h2>{{ LoveAlbumData.coverTitle }}</h2>
            <p class="cover-subtitle">{{ LoveAlbumData.coverSubtitle }}</p>
            <img :src="LoveAlbumData.coverStartImgUrl" class="cover-image">
          </div>
        </div>

        <!-- 翻页：动态渲染页面 -->
        <div v-for="(page, index) in LoveAlbumData.pages" :key="index" class="page"
          :class="{ '--left': index % 2 === 0, '--right': index % 2 !== 0 }">
          <div class="page-content">
            <h2 class="page-header">{{ page.title }}</h2>
            <div class="page-image" :style="{ backgroundImage: page.imgUrl ? 'url(' + page.imgUrl + ')' : 'none' }"
              @click="openImagePreview(page.imgUrl)">
            </div>
            <div class="page-text">{{ page.content }}</div>
            <button type="button" class="btn-comment" @click="openCommentModal(page.id)">评论</button>
            <div v-if="page.time" class="page-time">{{ page.time }}</div>
            <div class="page-footer">{{ index + 1 }}</div>
          </div>
        </div>

        <!-- 封底 -->
        <div class="page page-cover page-cover-bottom" data-density="hard">
          <div class="page-content">
            <h2 class="cover-end-title">THE END</h2>
            <p class="cover-subtitle">{{ LoveAlbumData.endCoverSubtitle }}</p>
            <img :src="LoveAlbumData.coverEndImgUrl" class="cover-image">
          </div>
        </div>

      </div>
    </div>

    <!-- 页脚 -->
    <div class="footer">
      <div class="footer-content">
        <div class="footer-nav">
          <button type="button" class="btn-prev" @click="flipPrev">
            <span class="heart-icon">❤</span> Previous
          </button>
          <div class="page-indicator">
            <!-- 进度条 -->
            <div class="progress-bar">
              <div class="progress" :style="{ width: (currentPage / (pageCount - 1)) * 100 + '%' }"></div>
            </div>
            <div class="progress-icon">❤{{ currentPage }}/{{ pageCount - 1 }}</div>
          </div>
          <button type="button" class="btn-next" @click="flipNext">
            Next <span class="heart-icon">❤</span>
          </button>
        </div>
        <div class="footer-info">
          <span class="info-label">© 2025 祝琳可爱&小满满</span>
          <span class="info-separator">|</span>
          <span class="info-label">由全宇宙的祝福驱动</span>
        </div>
      </div>
    </div>

    <!-- 使用图片预览组件 -->
    <ImageViewer :visible="visible"
      :imageUrls="imageUrls"
      :currentImageIndex="currentImageIndex"
      @update:visible="visible = $event"
      @update:currentImageIndex="currentImageIndex = $event"
    />

    <!-- 评论弹窗 -->
    <CommentModal 
      :visible="showCommentModal" 
      :initial-comments="comments"
      @update:visible="handleCommentClose($event)"
      @comment-submitted="handleNewComment"
      @comment-save="handleSaveComment"
    /> 

    <!-- 加载中 -->
    <Loading :visible="isLoading" text="加载中..." /> 
  </div>
</template>

<script>
// https://nodlik.github.io/StPageFlip/
// https://github.com/Nodlik/StPageFlip?tab=readme-ov-file
import { PageFlip } from 'page-flip';
import { LoveAlbumData, timeElapse } from '@/utils/LoveAlbumData';
import { fetchGistData, updateGistData } from '@/utils/chat';
import ImageViewer from '@/libs/image-viewer.vue';
import CommentModal from '@/libs/comment-modal.vue';
import Loading from '@/libs/loading.vue';

export default {
  name: 'LoveAlbum',
  components: {
    ImageViewer,
    CommentModal,
    Loading,
  },
  data() {
    return {
      LoveAlbumData,
      pageFlip: null, // page flip instance
      pageCount: 0, // 页面总数
      currentPage: 0, // 当前页码
      flipToIndex: 0, // 翻到指定页面的索引
      together: new Date("2024.03.17"), // 开始一起的日子
      togetherTimeResult: '', // 在一起的天数结果
      bookState: 'read', // 书本状态
      bookOrientation: 'landscape', // 书本方向
      visible: false, // 控制图片预览的显示
      imageUrls: [], // 图片预览的URL数组
      currentImageIndex: 0, // 当前预览图片的索引
      currentPageId: 0, // 当前页面的ID
      chatDataAllList: [], // 全部聊天数据列表
      showCommentModal: false, // 控制评论弹窗的显示
      comments: [], // 评论数据
      isLoading: false, // 加载中状态
    }
  },
  async mounted() {
    // 计算在一起的天数
      // this.handleTogetherTime();
      // 初始化翻页组件
      this.initPageFlip();
      // 封底处理
      this.handleBackCover();
  },
  methods: {
    // 处理评论发送逻辑
    async handleNewComment(newComment) {
      console.log('newComment----', newComment);
    },

    // 保存评论
    async handleSaveComment(newCommentList) {
      
      let saveCommentData = [];
      const currentChatData = this.chatDataAllList.find(item => item.id === this.currentPageId);
      if (currentChatData) {
        currentChatData.commentLists = newCommentList;
        // 构造完整的数据对象
        saveCommentData = [
          ...this.chatDataAllList
        ];
      } else {
        // 构造完整的数据对象
        const handleNewCommentData = {
          id: this.currentPageId,
          commentLists: newCommentList,
        };
        saveCommentData = [
          ...this.chatDataAllList,  // 第一个对象，原有数据数组
          { ...handleNewCommentData }  // 第二个对象，新评论数据对象
        ];
      }

      this.isLoading = true;
      
      // 保存评论
      try {
        await updateGistData(saveCommentData);
        this.$message.success('保存成功', 2000);
      } catch (error) {
        console.log('保存评论失败', error);
      } finally {
        this.isLoading = false;
      }
    },

    // 打开评论弹窗
    async openCommentModal(id) {
      // 数据清空
      // await updateGistData([]);
      // return;

      this.currentPageId = id;
      this.showCommentModal = true;
      this.isLoading = true;
      // 获取评论数据
      try{
        this.chatDataAllList = await fetchGistData();
      } catch (error) {
        this.chatDataAllList = [];
        this.$message.error('获取评论数据失败', 2000);
        this.showCommentModal = false;
        console.log('获取评论数据失败', error);
        return;
      } finally {
        this.isLoading = false;
      }

      const currentPageData = this.chatDataAllList.find(item => item.id === id);
      if (currentPageData) {
        this.comments = currentPageData?.commentLists || [];
      } 
    },

    // 关闭评论弹窗
    handleCommentClose(visible) {
      this.showCommentModal = visible;
      // 关闭弹窗时，清空评论数据
      this.comments = [];
    },
    
    // 打开图片预览
    openImagePreview(imgUrl) {
      // 获取所有页面中的图片URL
      this.imageUrls = this.LoveAlbumData.pages
        .filter(page => page.imgUrl)
        .map(page => page.imgUrl);

      // 当前预览图片的索引
      this.currentImageIndex = this.imageUrls.indexOf(imgUrl);

      // 显示预览
      this.visible = true;
    },

    // 初始化翻页组件
    initPageFlip() {
      this.pageFlip = new PageFlip(
        document.getElementById("demoBookExample"),
        {
          // startPage: 0, // 开始查看的页码
          // startZIndex: 0, // 书本的初始 z-index 值
          width: 550, // 书本宽度（单位：像素）
          // height: 900, // 书本高度 （单位：像素）
          height: 760, // 书本高度 （单位：像素）
          size: "stretch", // 书本是否会在父元素下拉伸, 页面尺寸模式："stretch"表示拉伸以适应容器 
          minWidth: 315, // 最小允许宽度（单位：像素）
          maxWidth: 1000, // 最大允许宽度（单位：像素）
          minHeight: 400, // 最小允许高度（单位：像素）
          maxHeight: 1350, // 最大允许高度（单位：像素）
          // swipeDistance: 5, // 滑动距离阈值 
          // drawShadow: boolean; // 翻页时是否绘制阴影
          // autoSize: true, // 如果为 true，父元素将与书本大小相等
           clickEventForward: false, // 设置点击子元素（按钮、链接）的事件传递
          //  showPageCorners: boolean; /** 如果为 true，当鼠标指针悬停在页面角落时，将折叠书本角落 */
          maxShadowOpacity: 0.6, // 阴影强度（1：最大强度，0：隐藏阴影）
          showCover: true, // 是否显示封面 如果为 true，第一页和最后一页将被标记为硬封面并以单页模式显示
          mobileScrollSupport: true, // 在移动设备上触摸书本时禁用内容滚动
          flippingTime: 800, // 翻页动画持续时间（单位：毫秒）
          usePortrait: true, // 启用竖屏模式切换
          useMouseEvents: false, // 使用鼠标和触摸事件进行翻页
          // disableFlipByClick: true, // 如果为 true，将锁定通过点击整个书本进行翻页，只能通过点击角落翻页
        }
      );

      this.pageFlip.loadFromHTML(document.querySelectorAll(".page"));
      this.pageCount = this.pageFlip.getPageCount();
      this.bookOrientation = this.pageFlip.getOrientation();

      // 翻页事件
      this.pageFlip.on("flip", (e) => {
        const index = LoveAlbumData.headerNav.findIndex((item) => item.includesIndex?.includes(e.data));
        if (index !== -1) {
          const findItem = LoveAlbumData.headerNav[index];
          this.flipToIndex = findItem.flipToIndex;
        } else {
          const findItem = LoveAlbumData.headerNav.find((item) => item.includesIndex?.includes(e.data));
          if (findItem) {
            this.flipToIndex = findItem.flipToIndex;
          }
        }

        this.currentPage = e.data;
      });

      // 书本状态变化事件
      this.pageFlip.on("changeState", (e) => {
        this.bookState = e.data;
      });

      // 书本方向变化事件
      this.pageFlip.on("changeOrientation", (e) => {
        this.bookOrientation = e.data;
      });
    },
    // 计算在一起的天数
    handleTogetherTime() {
      this.togetherTimeResult = timeElapse(this.together);
      // 优化：将定时器句柄保存，组件销毁时清除，防止内存泄漏
      // 组件卸载时需要清除定时器，防止内存泄漏
      this._togetherTimer = setInterval(() => {
        this.togetherTimeResult = timeElapse(this.together);
      }, 1000); // 1秒刷新一次即可，减少资源消耗
      // 在beforeDestroy/unmounted钩子中清除定时器
      this.$once('hook:beforeDestroy', () => {
        clearInterval(this._togetherTimer);
      });
      this.$once('hook:unmounted', () => {
        clearInterval(this._togetherTimer);
      });
    },
    // 封底处理
    handleBackCover() {
      const flipToIndexLast = LoveAlbumData.headerNav[LoveAlbumData.headerNav.length - 1].flipToIndex;
      const includesIndexLast = LoveAlbumData.headerNav[LoveAlbumData.headerNav.length - 1].includesIndex;
      const includesIndexLastSet = includesIndexLast[includesIndexLast.length - 1];
      LoveAlbumData.headerNav[LoveAlbumData.headerNav.length] = {
        title: "封底",
        flipToIndex: flipToIndexLast + 1,
        includesIndex: [includesIndexLastSet + 1],
      }
    },
    // 上一页
    flipPrev() {
      this.pageFlip.flipPrev();
    },
    // 下一页
    flipNext() {
      this.pageFlip.flipNext();
    },
    // 翻到指定页
    flipTo(index) {
      this.flipToIndex = index;
      const findItem = LoveAlbumData.headerNav[index];
      this.pageFlip.flip(findItem.includesIndex[0]);
    }
  },
  beforeDestroy() {
    if (this.pageFlip) {
      this.pageFlip.destroy();
    }
  }
}
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Ma+Shan+Zheng&display=swap');

* {
  box-sizing: border-box;
}

/* 优化时间信息区域样式 */
.love-time-info {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 12px;
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

/* 导航按钮容器样式（移动端优化） */
.page-nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* 导航按钮组（默认隐藏，移动端显示） */
.page-nav {
  display: none;
}

.time-title {
  font-family: 'Ma Shan Zheng', cursive;
  font-size: 18px;
  color: #ff4757;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
  background: linear-gradient(to right, #ff6b81, #ff4757);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  display: inline-block;
}

#elapseClock {
  font-family: 'Ma Shan Zheng', cursive;
  font-size: 18px;
  color: #ff6b81;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
  background: rgba(255, 255, 255, 0.8);
  padding: 8px 15px;
  border-radius: 20px;
  display: inline-block;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

#elapseClock:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.heart-icon {
  font-size: 20px;
  color: #ff6b81;
  margin-right: 5px;
  animation: heartbeat 1.5s ease infinite;
}

@keyframes heartbeat {
  0% {
    transform: scale(1);
  }

  25% {
    transform: scale(1.1);
  }

  50% {
    transform: scale(1);
  }

  75% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

/* 头部左侧区域样式 */
.header-left {
  width: 20%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  font-family: 'Ma Shan Zheng', cursive;
}

.header-left .avatar {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  border: 3px solid rgba(255, 255, 255, 0.8);
  object-fit: cover;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.header-left .avatar:hover {
  transform: scale(1.1);
  border-color: rgba(255, 107, 129, 0.8);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.album-title {
  font-size: 20px;
  color: #ff6b81;
  margin: 0;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}

/* 封面标题样式 */
.page.page-cover h2 {
  text-align: center;
  padding-top: 50%;
  font-size: 210%;
  font-family: 'Ma Shan Zheng', cursive;
}

/* 封面副标题样式 */
.page.page-cover .cover-subtitle {
  text-align: center;
  font-size: 120%;
  color: #8b5a2b;
  margin-top: 10px;
  font-family: 'Ma Shan Zheng', cursive;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}

/* 封面图片样式 */
.page.page-cover .cover-image {
  width: 80%;
  max-width: 300px;
  height: auto;
  display: block;
  margin: 10px auto 0;
  border-radius: 15px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
  border: 3px solid #f0e6d2;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.page.page-cover .cover-image:hover {
  transform: scale(1.05);
  box-shadow: 0 12px 35px rgba(0, 0, 0, 0.4);
}

/* 封面内容布局优化 */
.page.page-cover .page-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 20px;
  text-align: center;
}

.page.page-cover h2 {
  padding-top: 0;
  margin-bottom: 10px;
}


.love-album {
  width: 100%;
  margin: 0 auto;
  background: linear-gradient(135deg, #ff9a9e 0%, #fad0c4 100%);
}

.love-album .container {
  background-image: url(https://p6.itc.cn/images01/20230302/a8908863ce904c59b47b4756cd7efdcd.jpeg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  width: 100%;
  box-sizing: border-box;
  margin: 0 auto;
  position: relative;
}

/* 背景图片弱化处理 */
.love-album .container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 154, 158, 0.3);
  /* 使用主题色的半透明遮罩 */
  z-index: 1;
}

/* 确保翻书容器在遮罩之上 */
.love-album .container .flip-book {
  position: relative;
  z-index: 2;
}

/* 头部样式 */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  padding: 15px;
  background: linear-gradient(135deg, #fad0c4 0%, #ff9a9e 100%);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}

.header::before {
  content: "❤";
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 24px;
  color: rgba(255, 255, 255, 0.3);
}

/* 导航按钮容器样式 */
.page-index-info {
  width: 60%;
  max-height: 140px;
  display: flex;
  flex-direction: row;
  gap: 8px;
  padding: 5px 4px;
  flex-wrap: wrap;
  max-width: 100%;
  overflow-y: auto;
  white-space: nowrap;
  /* 防止文字换行 */
}

/* 自定义滚动条样式 */
.page-index-info::-webkit-scrollbar {
  height: 8px;
}

.page-index-info::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.3);
  border-radius: 4px;
}

.page-index-info::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #ff6b81, #ff8e9e);
  border-radius: 4px;
  border: 1px solid rgba(255, 255, 255, 0.5);
}

.page-index-info::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #ff4757, #ff6b81);
}

/* Firefox 滚动条样式 */
.page-index-info {
  scrollbar-width: thin;
  scrollbar-color: #ff6b81 rgba(255, 255, 255, 0.3);
}


/* 导航按钮样式 */
.page-index-btn {
  background: rgba(255, 255, 255, 0.8);
  border: 2px solid rgba(255, 107, 129, 0.3);
  padding: 10px 18px;
  border-radius: 15px;
  color: #ff6b81;
  font-family: 'Ma Shan Zheng', cursive;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  flex: 0 0 auto;
}

.page-index-btn::before {
  content: "❤";
  position: absolute;
  top: 0;
  left: 2px;
  font-size: 16px;
  opacity: 1;
  transition: opacity 0.3s ease;
}

.page-index-btn.active {
  transform: translateY(0);
  box-shadow: 0 2px 6px rgba(255, 107, 129, 0.3);
  background-color: rgba(255, 107, 129, 1);
  color: white;
}

/* 按钮样式 */
.btn-prev,
.btn-next {
  background: rgba(255, 255, 255, 0.8);
  border: none;
  padding: 8px 15px;
  border-radius: 20px;
  color: #ff6b81;
  font-family: 'Ma Shan Zheng', cursive;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 18px;
}

.btn-prev:hover,
.btn-next:hover {
  background: rgba(255, 255, 255, 1);
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* 页码样式 */
.page-current,
.page-total {
  color: #ff6b81;
  font-weight: bold;
}


/* 翻书容器样式 */
.flip-book {
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.5);
  margin: 0 auto;
  overflow: hidden;
  position: relative;
}

/* 页面基础样式 */
.page {
  padding: 20px;
  background-color: #f8f1e0;
  /* background-color: #fad0c4; */
  color: #5a3e36;
  border: solid 1px #b89a7a;
  overflow: hidden;
  /* 确保页面内容不溢出 */
  width: 100%;
  height: 100%;
}

/* 页面内容容器 */
.page .page-content {
  width: 100%;
