<template>
  <div class="pop-confirm" v-if="visible" @click.self="handleCancel">
    <div class="pop-confirm-content">
      <div class="pop-confirm-header">
        <h3>{{ title }}</h3>
      </div>
      <div class="pop-confirm-body">
        <p>{{ content }}</p>
      </div>
      <div class="pop-confirm-footer">
        <button class="btn-cancel" @click="handleCancel">取消</button>
        <button class="btn-confirm" @click="handleConfirm">确认</button>
      </div>
    </div>
  </div>
</template>

<script>
const PopConfirm = {
  name: 'PopConfirm',
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: '提示'
    },
    content: {
      type: String,
      default: '确定执行此操作吗？'
    }
  },
  methods: {
    handleConfirm() {
      this.$emit('confirm');
      this.$emit('update:visible', false);
    },
    handleCancel() {
      this.$emit('cancel');
      this.$emit('update:visible', false);
    }
  },
  install(Vue) {
    Vue.component('PopConfirm', this)
    
    const showPopConfirm = (options) => {
      return new Promise((resolve, reject) => {
        const PopConfirmConstructor = Vue.extend(this)
        const instance = new PopConfirmConstructor({
          propsData: {
            ...options,
            visible: true
          }
        }).$mount()
        document.body.appendChild(instance.$el)

        instance.$on('confirm', () => {
          resolve()
          instance.$el.remove()
        })

        instance.$on('cancel', () => {
          reject()
          instance.$el.remove()
        })
      })
    }

    Vue.prototype.$popConfirm = {
      show: (options) => showPopConfirm(options),
      success: (content, title = '操作成功') => showPopConfirm({ content, title, type: 'success' }),
      warning: (content, title = '警告') => showPopConfirm({ content, title, type: 'warning' }),
      error: (content, title = '错误') => showPopConfirm({ content, title, type: 'error' })
    }
  }
}

export default PopConfirm
</script>

<style scoped>
.pop-confirm {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease;
}

.pop-confirm-content {
  background-color: #fff;
  border-radius: 12px;
  padding: 20px;
  width: 80%;
  max-width: 400px;
  box-shadow: 0 4px 20px rgba(255, 107, 129, 0.2);
}

.pop-confirm-header h3 {
  color: #ff6b81;
  margin: 0 0 15px 0;
  font-size: 18px;
}

.pop-confirm-body p {
  color: #666;
  margin: 0 0 20px 0;
  font-size: 14px;
}

.pop-confirm-footer {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.btn-cancel, .btn-confirm {
  padding: 8px 16px;
  border-radius: 6px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.btn-cancel {
  background-color: #f5f5f5;
  color: #666;
  border: 1px solid #ddd;
}

.btn-confirm {
  background-color: #ff6b81;
  color: white;
  border: none;
}

.btn-cancel:hover {
  background-color: #eee;
}

.btn-confirm:hover {
  background-color: #ff5777;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}
</style>
