const gistId = '5884202b0d6a766ceffb92e88430b726'; // 替换为你的Gist ID
const gitHubPat = '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'; // 替换为你的GitHub令牌

/**
* 异步获取GitHub Gist数据
*
* @param {string} gistId - Gist的唯一标识符
* @param {string} gitHubPat - 用于访问Gist的GitHub个人访问令牌
* @returns {Promise<Object>} - 返回解析后的JSON对象，如果获取失败则抛出错误
* @throws {Error} - 如果无法获取Gist数据，则抛出错误
*/
const fetchGistData = async () => {
  try {
    const response = await fetch(`https://api.github.com/gists/${gistId}`, {
      headers: {
        'Authorization': `token ${gitHubPat}`
      }
    });
    const data = await response.json();
    const jsonData = data.files['chat.json']?.content || '[]';
    return JSON.parse(jsonData);
  } catch (error) {
    console.error('Error fetching Gist data:', error);
    throw error;
  }
};


/**
* 更新Gist数据
*
* @param {string} gistId Gist的ID
* @param {string} gitHubPat GitHub的授权令牌
* @param {Object} newData 需要更新的新数据
* @returns {Promise<Object>} 更新后的Gist数据
* @throws {Error} 如果更新失败，将抛出错误

*JSON.stringify 的第二个参数，通常用于指定一个“替换函数”（replacer function）或一个“包含数组”（用于筛选属性）。
如果传递 null，则表示不需要任何替换或筛选，直接序列化所有属性。
JSON.stringify 的第三个参数，用于控制缩进和格式化。
如果传递一个数字（如 2），则表示每一层嵌套的 JSON 数据会用指定数量的空格（这里是 2 个空格）缩进，使输出的 JSON 字符串更易读。
如果传递其他值（如字符串），则会用该字符串作为缩进字符（例如 \t 表示用制表符缩进）。
*/
const updateGistData = async (newData) => {
  try {
    const response = await fetch(`https://api.github.com/gists/${gistId}`, {
      method: 'PATCH',
      headers: {
        'Authorization': `token ${gitHubPat}`,
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        files: {
          'chat.json': {
            content: JSON.stringify(newData, null, 2) // 格式化JSON
          }
        }
      })
    });
    const data = await response.json();
    return data;
  } catch (error) {
    console.error('Error updating Gist:', error);
    throw error;
  }
};

