<template>
  <div class="comment-modal" v-if="visible">
    <div class="modal-overlay"></div>
    <div v-if="!vertify" class="vertify-container">
      <button class="btn-close" @click="closeModal">×</button>
      <img src="https://q8.itc.cn/q_70/images03/20250413/f325db0667654ecf82e8d904fb56f9ee.gif" class="vertify-img" />
      <input type="text" v-model="vertifyName" placeholder="请输入爱称验证" class="vertify-input" @input="changeInput">
      <div class="vertify-error" :class="{ 'vertify-success': vertifyContent.includes(vertifyName) }">{{ vertifyError }}</div>
      <button @click="handleVertifyName" class="vertify-btn">验证</button>
    </div>
    <div v-else class="modal-container">
      <div class="modal-header">
        <h2 class="modal-title">
          <span class="heart-icon">❤</span> 留言板
        </h2>
        <button class="btn-close" @click="closeModal">×</button>
      </div>

      <div class="comment-list">
        <div v-if="!comments.length">暂无评论哇！</div>
        <div v-for="(comment, index) in comments" :key="index" class="comment-item" :class="{ 'topped-comment': comment.isTopped }">
          <div class="comment-avatar">
            <img :src="comment.avatar" alt="用户头像">
          </div>
          <div class="comment-content">
            <div class="comment-user">
              <span class="user-name">{{ comment.name }}</span>
              <span class="comment-time">{{ comment.time }}</span>
              <span v-if="comment.isTopped" class="topped-badge">置顶</span>
            </div>
            <div v-if="editingCommentId !== comment.id" class="comment-text">{{ comment.content }}</div>
            <div v-else class="comment-edit">
              <textarea 
                v-model="editContent" 
                class="edit-textarea"
                placeholder="编辑评论内容"
              ></textarea>
              <div class="edit-actions">
                <button @click="confirmEdit(comment.id)" class="btn-confirm-edit">确定</button>
                <button @click="cancelEdit" class="btn-cancel-edit">取消</button>
              </div>
            </div>
            <div class="comment-actions" v-if="editingCommentId !== comment.id">
              <button v-if="!comment.isTopped" @click="topComment(comment.id)" class="btn-top">
                <span class="heart-icon">🔝</span> 置顶
              </button>
              <button v-else @click="cancelTop(comment.id)" class="btn-cancel-top">
                <span class="heart-icon">🔽</span> 取消置顶
              </button>
              <button @click="editComment(comment.id)" class="btn-edit">
                <span class="heart-icon">✏</span> 编辑
              </button>
              <button class="btn-delete" @click="deleteComment(comment.id)">
                <span class="heart-icon">❌</span> 删除
              </button>
            </div>
          </div>
        </div>
      </div>

      <div class="comment-input">
        <textarea 
          v-model="newComment" 
          placeholder="写下你的祝福..."
          class="input-text"
        ></textarea>
        <div class="comment-input-btn">
          <button 
            class="btn-submit" 
            @click="submitComment"
            :disabled="!newComment.trim()"
          >
            <span class="heart-icon">❤</span> 发送
          </button>
          <button 
            class="btn-save" 
            @click="saveComment"
            :disabled="!comments.length"
          >
            <span class="heart-icon">❤</span> 保存
          </button>
        </div>
        <p class="save-tip">有操作，记得保存哦！退出后再打开才能看到哦！</p>
      </div>
    </div>

  </div>
</template>

<script>
import { getNonceStr } from '@/utils/index';

export default {
  name: 'CommentModal',
  components: {
  },
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    initialComments: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      // 评论列表
      comments: [...this.initialComments],
      // 新评论
      newComment: '',
      // 验证爱称是否正确
      vertify: false,
      // 验证爱称输入内容
      vertifyName: '',
      // 验证爱称错误提示信息
      vertifyError: '',
      // 验证爱称内容
      vertifyContent: ['琳可爱', '小满满'],
      // 弹出确认框是否显示
      popconfirmVisible: false,
      // 编辑评论的ID
      editingCommentId: null,
      // 编辑评论内容
      editContent: ''
    }
  },
  methods: {
    // 编辑评论
    editComment(id) {
      const comment = this.comments.find(c => c.id === id);
      if (comment) {
        this.editingCommentId = id;
        this.editContent = comment.content;
      }
    },
    // 确认编辑评论内容
    confirmEdit(id) {
      const comment = this.comments.find(c => c.id === id);
      if (comment) {
        comment.content = this.editContent;
        this.editingCommentId = null;
        this.editContent = '';
      }
    },
    // 取消编辑评论内容
    cancelEdit() {
      this.editingCommentId = null;
      this.editContent = '';
    },
    // 删除评论
    deleteComment(id) {
      // 警告提示（带确认/取消回调）
      this.$popConfirm.success('确定删除吗？', '警告')
        .then(() => {
          this.comments = this.comments.filter(comment => comment.id !== id);
          this.$message.success('删除成功');  
        })
        .catch(() => {
          console.log('点击了取消')
          // 执行取消逻辑
        })
    },
    // 置顶评论
    topComment(id) {
      const comment = this.comments.find(c => c.id === id);
      if (comment) {
        comment.isTopped = true;
        this.comments = this.comments.filter(c => c.id !== id);
        this.comments.unshift(comment);
        this.$message.success('置顶成功');
      }
    },
    // 取消置顶
    cancelTop(id) {
      const comment = this.comments.find(c => c.id === id);
      if (comment) {
        comment.isTopped = false;
        // 移除评论
        this.comments = this.comments.filter(c => c.id !== id);
        // 根据时间戳重新插入到正确位置（假设评论对象有time字段）
        const insertIndex = this.comments.findIndex(c => new Date(c.time) > new Date(comment.time));
        if (insertIndex === -1) {
          this.comments.push(comment); // 如果没有更晚的评论，放到最后
        } else {
          this.comments.splice(insertIndex, 0, comment); // 插入到正确位置
        }
        this.$message.success('已取消置顶');
      }
    },
    // 验证爱称输入改变时候
    changeInput() {
      if (this.vertifyError) {
        this.vertifyError = '';
      }
    },
    // 验证爱称按钮点击事件
    handleVertifyName() {
      const vertifyName = this.vertifyName.trim();
      if (!vertifyName) {
        this.vertifyError = '不能为空，请输入您的爱称';
        return;
      }
      if (this.vertifyContent.includes(vertifyName)) {
        this.vertifyError = '太聪明呀您，答对了哦！';
        setTimeout(() => {
          this.vertify = true;
        }, 1000)
      } else {
        this.vertifyError = '输入不对，请回忆你的爱称哦！';
        return;
      }
    },
    // 关闭模态框
    closeModal() {
      this.$emit('update:visible', false);
    },
    // 发送
    submitComment() {
      if (!this.newComment.trim()) return;
      
      const newComment = {
        id: getNonceStr(),
        name: this.vertifyName,
        time: new Date().toLocaleString(),
        content: this.newComment,
        avatar: this.vertifyName === '琳可爱'
          ? 'https://i.postimg.cc/50PpqK8N/57.jpg'
          : 'https://i.postimg.cc/W3pXZcFH/58.jpg',
      };
      
      this.comments.push(newComment);
      this.newComment = '';
      this.$emit('comment-submitted', newComment);
    },
    // 保存评论
    saveComment() {
      this.$emit('comment-save', this.comments);
    }
  },
  watch: {
    // 监听评论变化，更新本地数据
    initialComments(newVal) {
      this.comments = [...newVal];
    }
  }
}
</script>

<style scoped>
