  color: rgb(252, 33, 66);
  font-family: 'Ma Shan Zheng', cursive;
}

.vertify-success {
  text-align: left;
  font-size: 18px;
  color: rgb(31, 170, 3);
  font-family: 'Ma Shan Zheng', cursive;
}

.modal-header {
  padding: 15px 20px;
  background: rgba(255, 255, 255, 0.8);
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(255, 107, 129, 0.3);
}

.modal-title {
  margin: 0;
  color: #ff6b81;
  font-family: 'Ma Shan Zheng', cursive;
  font-size: 20px;
  display: flex;
  align-items: center;
  gap: 5px;
}

.btn-close {
  background: none;
  border: none;
  color: #ff6b81;
  cursor: pointer;
  padding: 0;
  line-height: 1;
  position: absolute;
