}

.comment-list {
  max-height: 30vh;
  flex: 1;
  overflow-y: auto;
  padding: 15px;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: #ff6b81 rgba(255, 255, 255, 0.3);
  padding-right: 8px;
}

/* 自定义滚动条样式 */
.comment-list::-webkit-scrollbar {
  width: 4px;
}

.comment-list::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
}

.comment-list::-webkit-scrollbar-thumb {
