  position: relative;
}

.topped-comment::before {
  content: "🔝";
  position: absolute;
  left: -16px;
  top: 4px;
  font-size: 22px;
  animation: heartbeat 1.5s ease infinite;
}

.topped-badge {
  background: #ff6b81;
  color: white;
  padding: 2px 8px;
  border-radius: 10px;
  font-size: 12px;
