}

.comment-avatar img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid rgba(255, 107, 129, 0.5);
}

.comment-content {
  flex: 1;
}

.comment-user {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5px;
}

.user-name {
  font-weight: bold;
  color: #ff6b81;
  font-family: 'Ma Shan Zheng', cursive;
}

.comment-time {
  font-size: 12px;
  color: #8b5a2b;
}

.comment-text {
  width: 16em;
  color: #5a3e36;
  line-height: 1.5;
  word-wrap: break-word;
  text-align: left;
  font-size: 14px;
}

.comment-input {
  padding: 15px;
  background: rgba(255, 255, 255, 0.8);
  border-top: 1px solid rgba(255, 107, 129, 0.3);
}

