  width: 100%;
  min-height: 80px;
  padding: 10px;
  border: 2px solid rgba(255, 107, 129, 0.3);
  border-radius: 15px;
  resize: none;
  font-family: inherit;
  box-sizing: border-box;
  /* 新增以下样式移除黑色边框 */
  outline: none;
  transition: all 0.3s ease;
}

.input-text:focus {
  border-color: rgba(255, 107, 129, 0.8);
  box-shadow: 0 0 0 2px rgba(255, 107, 129, 0.2);
} 

.comment-input-btn {
  display: flex;
  justify-content: space-around;
  padding: 10px 10px 20px 10px;
}

.btn-submit, .btn-save {
  background: rgba(255, 255, 255, 0.8);
