  padding: 8px 20px;
  border-radius: 20px;
  color: rgba(255, 107, 129, 0.8);
  font-family: 'Ma Shan Zheng', cursive;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 18px;
}


.btn-submit:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.btn-save:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}


.save-tip {
  font-family: 'Ma Shan Zheng', cursive;
  font-size: 14px;
  color: rgba(255, 107, 129, 0.9);
  margin-top: 0;
