}

.comment-actions {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}

.btn-edit, .btn-delete {
  background: rgba(255, 255, 255, 0.8);
  border: 1px solid rgba(255, 107, 129, 0.3);
  border-radius: 15px;
  padding: 4px 12px;
  color: #ff6b81;
  cursor: pointer;
  transition: all 0.3s;
}

.btn-edit:hover, .btn-delete:hover {
  background: rgba(255, 107, 129, 0.1);
} 

.comment-edit {
  padding-right: 8px;
  margin-top: 10px;
}

.edit-textarea {
  width: 100%;
  min-height: 80px;
  padding: 10px 0 0 10px;
