  border: 1px solid rgba(255, 107, 129, 0.3);
  border-radius: 15px;
  padding: 4px 12px;
  color: #ff6b81;
  cursor: pointer;
  transition: all 0.3s;
  display: flex;
  align-items: center;
  gap: 5px;
}

.btn-top:hover {
  background: rgba(255, 107, 129, 0.1);
}

.btn-cancel-top {
  background: rgba(255, 255, 255, 0.8);
  border: 1px solid rgba(255, 107, 129, 0.3);
  border-radius: 15px;
  padding: 4px 12px;
