      买完门票我俩跟刚放暑假的小学生似的，就往景区里蹦跶。一进去立马看到充满田园气息的味道，稻苗长得比我还高，
      空气里都是青草和泥土的味儿，深吸一口感觉肺都被洗了个澡！当时就琢磨着，要是弄张桌子，泡上一壶功夫茶，爽呀。
      和贵楼、怀远楼这俩“土楼界顶流”必须打卡！我俩跟俩好奇宝宝似的，围着圆形土楼转圈圈，一会儿摸摸土墙，
