      记得那晚和姐姐，姐夫，老表他们在家自己打火锅，大家其乐融融吃完这一顿，有说有笑的，吃完回酒店休息。
      第二天杀去广州接阿水夫妇去佛山顺德玩，那才叫“大型叙旧现场”！阿水和琳可爱一见面就跟按了复读机似的，估计能从大学时谁偷偷暗恋隔壁班男生，聊到现在谁家孩子会叫“阿姨”了，俩人头凑在一起不停叽叽喳喳。
      到了顺德更是开启“逛吃暴走模式”！双皮奶滑滑的，鱼皮脆生生的。小白掏出手机拍大合照，琳可爱非要站C位，非要说“我是这里面美貌担当”。大家都笑笑不说话。
      虽然走的累，但看着大家脸上的笑，毕竟啊，最好的时光不就是这样嘛：有爱吃的美食，有能唠嗑的朋友，还有身边那个即是美貌担当的人，
