      看着前面黑压压的人群，跟着人流往前挪，过海关时把通行证递滴那么两下。顺利过关后坐地铁，微信款50多，俺和姨姨感叹，
      万恶的资本主义。我们先去喝早茶，不知道是大家以为太贵还是食量太小，都吃的小心翼翼。嘻嘻嘻。
