      我们在牌坊街逛得不亦乐乎，还拍了好多照片。可爱特意挑了个状元牌坊，站在那里摆姿势拍照。该说不说的，虽然做不成，但梦想也要有呢。俺的拍照技术没得说，拍出来的照片，把可爱拍出了几分状元的意境，仿佛穿越回古代一般。
