// 计算在一起天数
export const timeElapse = (date) => {
  if (!date) return;
  // 处理输入参数
  let startDate;
  if (date instanceof Date) {
    startDate = date;
  } else if (typeof date === "string") {
    // 处理日期字符串
    startDate = new Date(date.replace(/\./g, "-"));
  } else {
    console.error(
      "Invalid date parameter. Please use Date object or YYYY.MM.DD string format"
    );
    return;
  }

  // 确保日期有效
  if (isNaN(startDate.getTime())) {
    console.error("Invalid date. Please check your input");
    return;
  }

  // 获取当前时间
  const current = new Date();

  // 计算时间差（毫秒）
  let timeDiff = current - startDate;

  // 如果是未来日期，显示0
  if (timeDiff < 0) {
    timeDiff = 0;
  }

  // 转换为秒
  let seconds = Math.floor(timeDiff / 1000);

  // 计算天数
  const days = Math.floor(seconds / (3600 * 24));
  seconds = seconds % (3600 * 24);

  // 计算小时
  let hours = Math.floor(seconds / 3600);
  seconds = seconds % 3600;

  // 计算分钟
  let minutes = Math.floor(seconds / 60);
  seconds = seconds % 60;

  // 格式化显示（补零）
  hours = hours < 10 ? "0" + hours : hours;
  minutes = minutes < 10 ? "0" + minutes : minutes;
  seconds = seconds < 10 ? "0" + seconds : seconds;

  // 构建显示字符串
  const result = `${days}天 
                 ${hours}小时
                 ${minutes}分钟
                 ${seconds}秒`;

  return result;
};
