<template>
  <transition name="fade">
    <div v-if="visible" :class="['message-tip', type]" @click="hide">
      <div class="message-tip-content">
        <div class="icon"><i :class="iconClass"></i></div>
        <div class="content"><p>{{ message }}</p></div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'MessageTip',
  install(Vue) {
    // 全局组件注册：
    // 将当前组件(this)注册为全局组件，可以在任何地方使用<message-tip>标签。
    Vue.component('MessageTip', this)
    
    const showMessage = (type, message, duration = 3000) => {
      //  动态实例创建：
      // 使用Vue.extend基于当前组件创建构造函数
      // 通过构造函数创建实例并传入props
      // 手动挂载实例到内存中(未指定DOM元素)
      const MessageTipConstructor = Vue.extend(this)
      const instance = new MessageTipConstructor({
        propsData: { type, message, duration }
      }).$mount()
      // DOM挂载：
      document.body.appendChild(instance.$el)
      // 显示控制：
      instance.show()
    }

    // 全局API挂载：
    Vue.prototype.$message = {
      info: (message, duration) => showMessage('info', message, duration),
      success: (message, duration) => showMessage('success', message, duration),
      warning: (message, duration) => showMessage('warning', message, duration),
      error: (message, duration) => showMessage('error', message, duration)
    }
  },

  props: {
    type: {
      type: String,
      default: 'info',
      validator: value => ['info', 'success', 'warning', 'error'].includes(value)
    },
    message: {
      type: String,
      required: true
    },
    duration: {
      type: Number,
      default: 3000
    }
  },
  data() {
    return {
      visible: false
    }
  },
  computed: {
    iconClass() {
      return {
        info: 'icon-info',
        success: 'icon-check',
        warning: 'icon-warning',
        error: 'icon-error'
      }[this.type]
    }
  },
  methods: {
    show() {
      this.visible = true
      setTimeout(() => {
        this.hide()
      }, this.duration)
    },
    hide() {
      this.visible = false
    }
  }
}
</script>

<style scoped>
.message-tip {
  position: fixed;
  top: 50px;
  left: 50%;
  transform: translateX(-50%);
  min-width: 200px;
  padding: 10px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  z-index: 9999;
  cursor: pointer;
  font-size: 18px;
  font-family: 'Ma Shan Zheng', cursive;
}

.message-tip-content {
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.info {
  background-color: #f4f4f5;
  color: #909399;
}

.success {
  background-color: #f0f9eb;
  color: #67c23a;
}

.warning {
  background-color: #fdf6ec;
  color: #e6a23c;
}

.error {
  background-color: #fef0f0;
  color: #f56c6c;
}

.icon {
  margin-right: 10px;
  font-size: 18px;
}

/* 添加图标样式 */
.icon-info::before {
  content: "\f05a";
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
}

.icon-check::before {
  content: "\f00c";
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
}

.icon-warning::before {
  content: "\f071";
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
}

.icon-error::before {
  content: "\f057";
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>
